






export const engAlphabetToGeo  = (word: string): string =>{
if(!word) return '';


let georgian: string = ''
let letters= mappedLetters()
let curlyIndex = word.indexOf(')')

let lettersArray: Array<string> =word.split('')
if(curlyIndex){
    lettersArray.splice(0, curlyIndex+1)
    georgian +=  word.slice(0, curlyIndex+1)
}


lettersArray.forEach((letter: string)=>{
    const geoLetter = letters.get(letter)
    if(geoLetter){
        georgian +=  geoLetter 
    }else{
        georgian +=  letter 
    }
})

return georgian

}


const mappedLetters = ()=>  {
    const letters =new Map<string, string>()  
    letters.set('a', '')
    letters.set('b', '')
    letters.set('c', '')
    letters.set('C', '')
    letters.set('d', '')
    letters.set('e', '')
    letters.set('f', '')
    letters.set('g', '')
    letters.set('h', '')
    letters.set('i', '')
    letters.set('j', '')
    letters.set('k', '')
    letters.set('l', '')
    letters.set('m', '')
    letters.set('n', '')
    letters.set('o', '')
    letters.set('p', '')
    letters.set('q', '')
    letters.set('r', '')
    letters.set('R', '')
    letters.set('s', '')
    letters.set('S', '')
    letters.set('t', '')
    letters.set('T', '')
    letters.set('u', '')
    letters.set('v', '')
    letters.set('w', '')
    letters.set('W', '')
    letters.set('x', '')
    letters.set('y', '')
    letters.set('z', '')
    letters.set('Z', '')
    return letters
}